import React, { useState, useEffect, useRef } from 'react'
import PromptUser from './components/PromptUser'
import Resume from './components/Resume'
import NonExistentPage from './components/NonExistentPage'
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom'
import "./App.css"
import { useNavigate, Link } from 'react-router-dom'

const converter = require('number-to-words')

function App() {
 
  const [userInfo, setUserInfo] = useState({
  })
  const [buttonClicked, setButtonClicked] = useState(false)
 

  const [source, setSource ] = useState('')

  const random_number = Math.floor(Math.random()*10e4)
  const random_alpha_num = converter.toWords(random_number)
  const getRandomNumber = (randNum) => {
    return converter.toWords(randNum)
  }
  const generateImage = () => {
    setSource(`https://robohash.org/stefan-${random_alpha_num}`)
    console.log(source)
  }
  useEffect(() => {
    generateImage()
  }, [])
// console.log(random_alpha_num)
// console.log(source)

function handleChange(e){
  setUserInfo(prevInfo =>  {
    return {
      ...prevInfo, 
      [e.target.name]: e.target.value
    }
  })
  // console.log(userInfo)

}
  
  return (
<Router>
  
    <Routes>
      <Route path = '/' element = {<PromptUser handleChange = {handleChange}/>}/>
      <Route path = '/resume' element = {<Resume userObjectHandler = {setUserInfo} userObject = {userInfo} imageSource={source}/>}/>
      <Route path = '*' element = {<NonExistentPage/>}/>
    </Routes>
</Router>
  );
}

