import React, { useEffect } from 'react'
import ResumeHeader from './ResumeHeader'


const Resume = ({imageSource, name, age, email, education, graduationYear, experience, profileInfo, userObject}) => {
    
  
    // console.log('Type of userObjectHandler', typeof userObjectHandler)
    console.log('RESUME SAYS: ', userObject)
  return (
    <div className='resume-card'>
        <ResumeHeader imageSource={imageSource} userObject = {userObject}/>

        <div className="grid">
          <div className = 'grid-item'>

            <div className = 'contact-list'>
            <ul>
              <li className = 'phoneNumber'>{userObject.phoneNumber}</li>
              <li className = 'email'>{userObject.email}</li>
            {userObject.website ? <li className = 'website'>{userObject.website}</li> : ''}
            </ul>
            </div>
            <div className = 'profile-info'>
              <h3>About Me</h3>
              <p>{userObject.profile}</p>
            </div>
         



          </div>
        {/* /////////////////////// */}
          <div className = 'grid-item row2'>

          </div>
        {/* /////////////////////// */}
          <div className = 'grid-item row3'>

          </div>
        {/* /////////////////////// */}

          <div className = 'grid-item row4'>

          </div>
        </div>
  
    </div>
  )
}

export default Resume