import React from 'react'

const ResumeHeader = ({imageSource, name, age, email, userObject}) => {
  return (
    <div>
        <div className = 'basic-info'>
        <h3>{userObject.firstName} {userObject.lastName}</h3>
        <p>{userObject.age} years old</p>
     
        </div>
        
        <img alt = {`${userObject.firstName}'s profile`} src = {imageSource}/>
    </div>
  )
}

export default ResumeHeader