import React from 'react'
import { useNavigate, Link } from 'react-router-dom'
import InputItem from './InputItem'
const PromptUser = ({handleChange}) => {
    const navigate = useNavigate()

  return (
    <div className='prompt-container'>
      <div className='inputs'>
 <InputItem className = 'first-name' onChange = {handleChange} label = 'First Name' placeholder = 'Enter your first name' name = 'firstName'/>
 <InputItem className = 'last-name' onChange = {handleChange} label = 'Last Name' placeholder = 'Enter your last name' name = 'lastName'/>
 <InputItem className = 'email' onChange = {handleChange} label = 'Email' placeholder = 'Enter your email' name = 'email'/>
 <InputItem className = 'phone' onChange = {handleChange} label = 'Phone #' placeholder = 'Enter your #' name = 'phoneNumber'/>
 <InputItem className = 'website' onChange = {handleChange} label = 'Website' placeholder = '*OPTIONAL*' name = 'website'/>
 <InputItem className = 'birth-date' onChange = {handleChange} label = 'D.O.B' type = 'date' name = 'DOB'/>
 <InputItem className = 'age' onChange = {handleChange} label = 'Age' type = 'number' name = 'age' placeholder='Enter your age'/>
 <InputItem className = 'profile' onChange = {handleChange} placeholder = 'Write about yourself' name = 'profile' isTextArea = {true}/>
 <button className='generate-button' onClick={() => {
        navigate('/resume')
    }}>Generate Resume!</button>

   </div>

</div>
  )
}

export default PromptUser